body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.delete-icon {
  font-size: 1em;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  color: rgba(128, 128, 128, 0.582);
}

.book-card h2 {
  font-family: 'Montserrat', sans-serif;
}
.navLink {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  z-index: 10;
  color: rgb(2, 2, 29);
}
.navLink:hover {
  text-decoration: none;
  color: #f7576f;
}
@media only screen and (max-width: 600px) {
  .nav-bar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
}
.search-box {
  margin-top: 6vh;
  -webkit-align-items: center;
          align-items: center;
}

.bookQuote {
  margin-top: 10px;
  font-size: 16px;
  font-style: italic;
  color: #757474;
  font-family: 'Montserrat', sans-serif;

  padding: 10px;
}

.search-label {
  font-family: 'Montserrat', sans-serif;
  font-size: 25px;
  letter-spacing: 0px;
}

.search-button {
  border: 2px solid #ff0a87;
  box-shadow: 0px 2px 4px rgba(121, 121, 121, 0.616);
  background-color: inherit;
  font-weight: 600;
  border-radius: 20px;
  font-size: 14px;
  vertical-align: middle;
  font-family: 'Montserrat', sans-serif;
}

.fa-search {
  font-size: 1.2em;
  color: #ff0a87;
  padding: 5px;
  text-align: left;
}

.search-button:hover {
  background-color: lightseagreen;
  transition: 0.5s;
}

.input-row input {
  background-color: white;
}

input[type="text"] {
  background: #f1f1f1;
}

@media only screen and (max-width: 600px) {
  .search-label {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
  }

  .search-button {
    margin-top: 5px;
    margin: auto;
  }
  .bookQuote {
    font-size: 13px;
  }
}
.reader-img {
  height: auto;
  width: 100%;
}

.hero-text h4 {
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
}

.hero-container {
  margin-top: 10vh;
}

q {
  color: teal;
}

