  background-color: inherit;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  cursor: pointer;
}

.fas.fa-search {
  color: #f7576f;
}
.book-input {
  background-color: none !important;
  position: absolute;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;

