.book-in-db {
  font-family: 'Montserrat', sans-serif;
    color: #8a8a8a;
    text-align: center;
    font-size: 14px;
  }
  .book-item {
    font-family: 'Montserrat', sans-serif;
    display: -webkit-flex;
    display: flex;
    padding: 5px;
  }
  .book-card {
    margin-top: 10vh;
  }
  .book-cover {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
  }
  
  .card {
    border: none;
  }
  
  .book-card h5 {
    font-weight: bold;
    font-size: 22px;
    color: black;
  }
  
  .book-card h6 {
    font-size: 16px;
    color: gray;
  }
  .book-description {
    font-size: 15px;
    color: #363636;
  }
  .hide {
    display: none;
  }
  .save-button {
    background-color: inherit;
    border: 1px solid silver;
    border-radius: 5px;
    color: gray;
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    padding: 10px;
  }
  .save-button:hover {
    background-color: #f7576f;
    transition: 1s;
    color: white;
